<script>
  import { createEventDispatcher } from 'svelte';

  const dispatch = createEventDispatcher();

  let activeTab = 'Home';

  function setActiveTab(tab) {
    activeTab = tab;
    console.log(activeTab);
    dispatch('tabChange', { tab });
  }

  console.log(activeTab);
</script>

<nav>
<ul>
  <li>
    <button
      class:selected={activeTab === 'Home'}
      on:click={() => setActiveTab('Home')}
    >Home</button>
  </li>
  <li>
    <button
      class:selected={activeTab === 'About'}
      on:click={() => setActiveTab('About')}
    >About</button>
  </li>
  <li>
    <button
      class:selected={activeTab === 'Contact'}
      on:click={() => setActiveTab('Contact')}
    >Contact</button>
  </li>
</ul>
</nav>

